import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import community from "../../../media/community.png";
import axios from "axios";

const Home = () => {
  const [login, setLogin] = useState({ loggedIn: false, id: "" });
  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_BACKEND_URL}/users`)
      .then((res) => {
        if (res.data.validity) {
          setLogin({ loggedIn: true, id: res.data.data });
        }
      })
      .catch((err) => console.log(err));
  }, []);
  return (
    <div className="home">
      <div className="home__left">
        <p>
          Lorem Ipsum is simply a dummy text of the printing and typesetting
          industry. Lorem Ipsum has been the industry's standard dummy text ever
          since the 1500s.
        </p>
        <div className="home__left__ButtonContent">
          <Link to={`/${login.id === "" ? "loginRegister" : "userProfile"}`}>
            <div className="button">
              {login.id === "" ? "Get Started" : "My Profile"}
            </div>
          </Link>
        </div>
      </div>
      <div className="home__right">
        <img src={community} height="300px" alt="community_image" />
      </div>
    </div>
  );
};
export default Home;
